/* Notification Container */
.notification-container {
  position: relative;
  display: inline-block;
}

/* Notification Bell Button */
.notification-bell {
  background: transparent;
  border: 1px solid #158638;
  color: #158638 !important;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
}

.notification-bell:hover {
  background: rgba(21, 134, 56, 0.1);
  color: #158638 !important;
  transform: translateY(-1px);
}

.bell-icon {
  width: 20px;
  height: 20px;
  color: currentColor;
}

/* Unread Badge */
.unread-badge {
  position: absolute;
  top: -5px;
  right: -5px;
  background: #dc2626;
  color: white;
  font-size: 0.75rem;
  font-weight: 600;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Poppins', sans-serif;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

/* RTL Badge Position */
.notification-container.rtl .unread-badge {
  right: auto;
  left: -5px;
}

/* Notification Backdrop */
.notification-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  z-index: 9999;
}

.notification-backdrop.open {
  opacity: 1;
  visibility: visible;
}

/* Notification Dropdown */
.notification-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  width: 400px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  opacity: 0;
  visibility: hidden;
  transform: translateY(-10px) scale(0.95);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1000;
  margin-top: 0.5rem;
}

.notification-dropdown.open {
  opacity: 1;
  visibility: visible;
  transform: translateY(0) scale(1);
}

/* RTL Dropdown Position */
.notification-container.rtl .notification-dropdown {
  right: auto;
  left: 0;
}

/* Dropdown Header */
.dropdown-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1.5rem 1rem;
  border-bottom: 1px solid #e5e7eb;
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  border-radius: 16px 16px 0 0;
}

.header-title {
  font-size: 1.125rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0;
}

.mark-all-read-btn {
  background: none;
  border: none;
  color: #158638;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  padding: 0.5rem 0.75rem;
  border-radius: 8px;
  transition: all 0.2s ease;
}

.mark-all-read-btn:hover {
  background-color: rgba(21, 134, 56, 0.1);
}

/* Error Message */
.error-message {
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
  padding: 1rem 1.5rem;
  margin: 0 1.5rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 0.875rem;
  margin-top: 1rem;
}

.error-icon {
  flex-shrink: 0;
  color: #dc2626;
}

/* Notifications List */
.notifications-list {
  max-height: 400px;
  overflow-y: auto;
  padding: 0 1.5rem;
}

/* Loading State */
.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  color: #6b7280;
}

.loading-spinner {
  animation: spin 2s linear infinite;
  color: #158638;
  margin-bottom: 0.5rem;
}

.loading-text {
  font-size: 0.875rem;
  margin: 0;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* Notification Item */
.notification-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  padding: 1rem 0;
  border-bottom: 1px solid #f1f5f9;
  transition: all 0.2s ease;
  animation: slideIn 0.3s ease forwards;
}

.notification-item:last-child {
  border-bottom: none;
}

.notification-item:hover {
  background-color: #f8fafc;
  margin: 0 -1.5rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

/* Unread Notification Styling */
.notification-item.unread {
  background-color: #f0f9ff;
  margin: 0 -1.5rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  border-left: 3px solid #158638;
}

/* Read Notification Styling */
.notification-item.read {
  background-color: #f9fafb;
  opacity: 0.8;
}

.notification-item.read .notification-title {
  color: #6b7280;
}

.notification-item.read .notification-message {
  color: #9ca3af;
}

.notification-item.read .notification-time {
  color: #d1d5db;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-10px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Notification Icon */
.notification-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  font-size: 1.25rem;
  font-weight: 600;
}

.notification-info {
  background: linear-gradient(135deg, #158638 0%, #09365F 100%);
  color: white;
}

.notification-success {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  color: white;
}

.notification-warning {
  background: linear-gradient(135deg, #d97706 0%, #b45309 100%);
  color: white;
}

.notification-error {
  background: linear-gradient(135deg, #dc2626 0%, #b91c1c 100%);
  color: white;
}

.icon-text {
  font-size: 1rem;
}

/* Notification Content */
.notification-content {
  flex: 1;
  min-width: 0;
}

.notification-title {
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 0.5rem 0;
  font-size: 0.875rem;
  line-height: 1.4;
}

.notification-message {
  color: #64748b;
  margin: 0 0 0.5rem 0;
  font-size: 0.875rem;
  line-height: 1.4;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.notification-time {
  color: #9ca3af;
  font-size: 0.75rem;
  font-weight: 500;
}

/* Notification Actions */
.notification-actions {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-shrink: 0;
}

.mark-read-btn {
  background: none;
  border: none;
  color: #158638;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mark-read-btn:hover {
  background-color: rgba(21, 134, 56, 0.1);
}

/* Empty State */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  color: #9ca3af;
  text-align: center;
}

.empty-icon {
  margin-bottom: 1rem;
  opacity: 0.5;
}

.empty-text {
  font-size: 0.875rem;
  margin: 0;
}

/* Dropdown Footer */
.dropdown-footer {
  padding: 1rem 1.5rem 1.5rem;
  border-top: 1px solid #e5e7eb;
  background: #f9fafb;
  border-radius: 0 0 16px 16px;
  text-align: center;
}

.view-all-link {
  color: #158638;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.875rem;
  transition: color 0.2s ease;
}

.view-all-link:hover {
  color: #09365F;
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  /* Show backdrop only on mobile */
  .notification-backdrop {
    display: block;
  }
  
  .notification-dropdown {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.95);
    width: 90vw;
    max-width: 400px;
    right: auto;
    margin-top: 0;
    z-index: 10000;
  }
  
  .notification-dropdown.open {
    transform: translate(-50%, -50%) scale(1);
  }
  
  .notification-container.rtl .notification-dropdown {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.95);
    right: auto;
    margin-top: 0;
  }
  
  .notification-container.rtl .notification-dropdown.open {
    transform: translate(-50%, -50%) scale(1);
  }
  
  .dropdown-header,
  .notifications-list,
  .dropdown-footer {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  
  .notification-item:hover,
  .notification-item.unread {
    margin: 0 -1rem;
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

/* Hide backdrop on larger screens */
@media (min-width: 769px) {
  .notification-backdrop {
    display: none;
  }
}

@media (max-width: 480px) {
  .notification-dropdown {
    width: 95vw;
    max-width: 350px;
  }
  
  .notification-container.rtl .notification-dropdown {
    width: 95vw;
    max-width: 350px;
  }
}

/* RTL Specific Styles */
.notification-container.rtl .notification-dropdown {
  direction: rtl;
}

.notification-container.rtl .notification-item:hover,
.notification-container.rtl .notification-item.unread {
  margin: 0 -1.5rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.notification-container.rtl .notification-item.unread {
  border-left: none;
  border-right: 3px solid #158638;
}

.notification-container.rtl .mark-all-read-btn {
  margin-left: 0;
  margin-right: auto;
}

.notification-container.rtl .notification-actions {
  order: -1;
}

/* Animation Delays for Notification Items */
.notification-item:nth-child(1) { animation-delay: 0.05s; }
.notification-item:nth-child(2) { animation-delay: 0.1s; }
.notification-item:nth-child(3) { animation-delay: 0.15s; }
.notification-item:nth-child(4) { animation-delay: 0.2s; }
.notification-item:nth-child(5) { animation-delay: 0.25s; }
